.Projects{
  padding: 2.5%; 
}
.container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  
    box-shadow: 0 4px 8px 8px rgba(0,0,0,0.2);
    transition: 0.3s;
  }
  
  .child {
    display: inline-block;
    font-family: Montserrat, sans-serif;
    font-size: 20px;
    color: #FFF;
   padding-left: 2%;
   
    flex: 1;
  }
  .child:first-child {
    background: #363D48;
  }
  .child:second-child {
    background: #282D35;
  }


@media all and (max-width: 800px){
  
  .sub_style{
      font-size: 12px;
  }

}
  